#Header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  align-items: top;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  padding-bottom: 10vmin;
}

h1, h3, #break-length, #session-length {
  color: seashell;
}

.Break-Sesh {
  display: flex;
  justify-content: space-evenly;
  align-items: top;
  text-align: center;
  min-height: 70vmin;
  background-color: #282c34;
}

.incDec {
  background-color: yellow;
  font-size: 200%;
  font-weight: bold;
}

.flex-third {
  height: 40vmin;
  width: 30vmin;
  border-radius: 5px;
}

#timer {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#time-left {
  height: 25vmin;
  font-size: 300%;
  text-align: center;
  line-height: 300%;
  background-color: darkslateblue;
  color: limegreen;
  border-radius: 5px;
}

.timer-buttons {
  display: flex;
  justify-content: space-between;
  align-items: top;
  font-size: 150%;
  color: whitesmoke;
}

#start_stop {
  background-image: linear-gradient(to right,rgb(43, 197, 43),rgb(196, 196, 34));
  height: 6vmin;
  width: 17vmin;
  border-radius: 5px;
}
#reset {
  background-color: rgb(240, 4, 51);
  height: 6vmin;
  width: 10vmin;
  border-radius: 5px;
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 20vmin;
  width: 20vmin;
  pointer-events: none;
}

@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg)
  }
}
